:root{--c-bg:#FFFFFF;--c-bg-hover:#fafafa;--c-bg-active:whitesmoke;--c-main:#0D1117;--c-link:#53cbcf;--c-link-visited:#e7961b;--c-border:#aeb4b6;--c-shadow:rgba(13, 17, 23, 0.4)}@media (prefers-color-scheme: dark){:root{--c-bg:#0D1117;--c-bg-hover:#11161e;--c-bg-active:#141b24;--c-main:whitesmoke;--c-link:#53cbcf;--c-link-visited:#e7961b;--c-border:#aeb4b6;--c-shadow:rgba(255, 255, 255, 0.4)}}body{background-color:var(--c-bg);color:var(--c-main)}body a{color:var(--c-link)}body a:visited{color:var(--c-link-visited)}div.no-wrap{word-break:keep-all}a.gh-card>img{display:inline-block;max-width:100%}main{display:flex;gap:1em;max-width:1600px;margin:0 auto;flex-direction:row;font-size:20px;line-height:1.25em;font-family:"Kiwi Maru",Arial,Helvetica,sans-serif;font-weight:300}main h1,main h2,main h3,main h4,main h5,main h6{font-family:"Makinas-4-Flat",Arial,Helvetica,sans-serif}@media screen and (max-width: 960px){main{flex-direction:column;line-height:1.5em}}main>#layout_sidebar{max-width:380px;display:flex;flex-direction:column;align-items:center}@media screen and (max-width: 960px){main>#layout_sidebar{max-width:40em;margin:0 auto}}main>#layout_sidebar>*{margin-left:.5em;margin-right:.5em}main>#layout_sidebar>h1{line-height:1.25em}main>#layout_sidebar>section>h2{text-align:center}main>#layout_sidebar>section>h2::after{display:block;width:100%;height:2px;margin-top:.25em;content:"";background:linear-gradient(to right, #53cbcf, #267ed1)}main>#layout_sidebar>img.profile{margin-top:2em;max-width:300px;width:100%;border-radius:2.5%}main>#layout_main>section>h2{text-align:center}main>#layout_main>section>h2::after{display:block;width:100%;height:2px;margin-top:.25em;content:"";background:linear-gradient(to right, #53cbcf, #267ed1)}main>#layout_main article{border-radius:1em;box-shadow:0px 0px 5px var(--c-shadow);padding:1em}@media (prefers-color-scheme: dark){main>#layout_main article{box-shadow:unset;border:2px solid var(--c-border)}}main>#layout_main article+article{margin-top:.5em}@media screen and (max-width: 960px){main>#layout_main{max-width:60em}}